import { Component, OnInit, ViewEncapsulation } from "@angular/core";
import { FormArray, FormBuilder, FormGroup, Validators } from "@angular/forms";
import { ActivatedRoute, Router } from "@angular/router";
import { NgbModal } from "@ng-bootstrap/ng-bootstrap";
import { ColorTypeService } from "app/modules/design/service/colorType.service";
import { ItemMasterService } from "app/modules/item-master/service/itemMaster.service";
import { UserService } from "app/modules/user/service/user.service";
import { ModalAddCustomerComponent } from "app/shared/modal-add-customer/modal-add-customer.component";
import { SweetalertComponent } from "app/shared/sweetalert/sweetalert.component";
import { WholeSaleService } from "../../service/wholeSale.service";
import { ImageModalComponent } from "app/shared/image-modal/image-modal.component";
import { OrderService } from "app/modules/order/service/order.service";
import { ToastrService } from 'ngx-toastr';

@Component({
  selector: "app-whole-sale",
  templateUrl: "./form-whole-sale.component.html",
  styleUrls: ["./form-whole-sale.component.scss"],
  encapsulation: ViewEncapsulation.None,
})
export class FormWholeSaleComponent implements OnInit {
  contentHeader: object;
  public submitted = false;
  public editable = false;
  public wholeSaleForm: FormGroup;
  public id;
  public customerData = {
    customerName: null,
    customerNumber: null,
  };
  public totalAmount = {
    totalDeliveryCost: null,
    amountReceived: 0,
    totalNetAmount: null,
    netBalanceAmount: null,
  };
  itemMaster;
  colorType;
  userDetail;
  items = [];
  checkQuantity = [];
  public totalQuantity;
  // multiple Image Upload
  public orderImages = [];
  public editOrderImage = [];
  onFileChange(event, index) {
    if (event.target.files && event.target.files[0]) {
      this.items[index].itemImage = event.target.files[0].name;
      this.wholeSaleForm.controls.details.value[index].itemImage =
        event.target.files[0].name;
      let reader = new FileReader();
      reader.onload = (event: any) => {
        this.editOrderImage[index] = event.target.result;
        this.orderImages[index] = event.target.result;
      };
      reader.readAsDataURL(event.target.files[0]);
    }
  }

  constructor(
    private _itemMaster: ItemMasterService,
    private _colorType: ColorTypeService,
    private _customerService: UserService,
    private fb: FormBuilder,
    private _router: Router,
    private modalService: NgbModal,
    private _sw: SweetalertComponent,
    private _wholeSaleService: WholeSaleService,
    private route: ActivatedRoute,
    private _orderService: OrderService,
    private toastr: ToastrService
  ) {}

  nowReceivedAmount
  nowReceived(amount){
    this.totalAmount.amountReceived= +this.nowReceivedAmount + +amount
    this.totalAmountCalculator()
  }


  getItemDetail(value, index) {
    this.items[index].itemUOM = value.UOM;
    this.items[index].itemArticle = value.article;
    this.items[index].itemColor = value.color;
    this.checkQuantity[index].itemName = value.name;
    this.checkQuantity[index].closingQuantity = value.closingQuantity;
  }

  ngOnInit(): void {
    this.id = +this.route.snapshot.paramMap.get("id");
    if (this.id > 0) {
      this.editable = true;
      this.getDetailById();
    }
    this.getItemMaster();
    this.getColorType();
    this.getUserDetails();
    this.makeReactiveForm();

    this.contentHeader = {
      headerTitle: "Whole Sale ",
      actionButton: true,
      breadcrumb: {
        type: "",
        links: [
          {
            name: "Whole Sale Fabric",
            isLink: true,
            link: "/",
          },
          {
            name: "Entry",
            isLink: false,
          },
        ],
      },
    };
  }

  getDetailById() {
    this._wholeSaleService.getDetailById({ id: this.id }).subscribe((res) => {
      this.wholeSaleForm.patchValue({
        transactionDate: res.data.transactionDate,
        deliveryDate: res.data.deliveryDate,
        supplierName: res.data.supplierName,
        supplierNumber: res.data.supplierNumber,
        personName: res.data.personName,
        totalDeliveryCost: res.data.totalDeliveryCost,
        amountReceived: res.data.amountReceived,
        netBalanceAmount: res.data.netBalanceAmount,
        totalNetAmount: res.data.totalNetAmount,
        note: res.data.note,
      });
      this.nowReceivedAmount =res.data.amountReceived
      res.data.details.forEach((item, i) => {
        this.getDetailFormGroup(item, i);
      });
    });
  }

  getDetailFormGroup(item, i) {
    const detailFormGroup = this.fb.group({
      id: [item.id],
      itemMaster: [item.itemMaster],
      itemUOM: [item.itemUOM],
      itemArticle: [item.itemArticle],
      itemColor: [item.itemColor],
      itemQuantity: [item.itemQuantity],
      itemPrice: [item.itemPrice],
      itemAmount: [item.itemAmount],
      itemDelivery: [item.itemDelivery],
      itemNetSalePrice: [item.itemNetSalePrice],
      itemNetAmount: [item.itemNetAmount],
      itemImage: [item.itemImage],
    });

    this.editOrderImage[i] =
      item.itemImage == null
        ? null
        : `assets/images/wholesale/${item.itemImage}`;
    (this.wholeSaleForm.get("details") as FormArray).push(detailFormGroup);
    this.items.push(detailFormGroup.value);
  }

  get f() {
    return this.wholeSaleForm.controls;
  }
  makeReactiveForm() {
    this.wholeSaleForm = this.fb.group({
      transactionDate: ["", [Validators.required]],
      deliveryDate: ["", [Validators.required]],
      supplierName: [null, [Validators.required]],
      supplierNumber: [null, [Validators.required]],
      personName: [""],
      totalDeliveryCost: ["", [Validators.required]],
      amountReceived: ["", [Validators.required]],
      totalNetAmount: ["", [Validators.required]],
      netBalanceAmount: ["", [Validators.required]],
      note: [""],
      details: this.fb.array([]),
    });
    if (!this.editable) {
      for (let i = 0; i < 3; i++) {
        this.addItem();
      }
    }
  }

  addItem() {
    const itemsArray = this.wholeSaleForm.get("details") as FormArray;
    this.items.push(this.createItemFormGroup().value);
    itemsArray.push(this.createItemFormGroup());
    this.checkQuantity.push({ itemName: null, closingQuantity: null });
  }

  // Create a FormGroup for an item
  createItemFormGroup() {
    return this.fb.group({
      itemMaster: [null],
      itemUOM: [""],
      itemArticle: [""],
      itemColor: [null],
      itemQuantity: [""],
      itemPrice: [""],
      itemAmount: [""],
      itemDelivery: [""],
      itemNetSalePrice: [""],
      itemNetAmount: [""],
      itemImage: [null],
    });
  }

  // Getter for the details FormArray
  get details() {
    return this.wholeSaleForm.get("details") as FormArray;
  }

  /**
   * DeleteItem
   *
   * @param id
   */
  deleteItem(id) {
    for (let i = 0; i < this.details.controls.length; i++) {
      if (this.details.controls.indexOf(this.details.controls[i]) === id) {
        this.details.value.splice(i, 1);
        this.details.controls.splice(i, 1);
        this.items.splice(i, 1);
        this.checkQuantity.splice(i, 1);
        break;
      }
    }
  }

  imagesUpload() {
    const multiImage = {
      imageName: this.items.map((name) => name.itemImage),
      file: this.orderImages,
      filePath: "wholesale",
    };
    this._orderService.uploadMultipleImage(multiImage).subscribe();
  }

  onSubmit(formData) {
    this.submitted = true;
    if (this.wholeSaleForm.invalid) {
      return;
    }

    this.imagesUpload();
    if (this.editable) {
      this.updateFormDetail(formData);
    } else {
      this.insertFormDetail(formData);
    }
  }

  insertFormDetail(formData) {
    if (this.checkQuantity.some((qty) => qty.closingQuantity <= 0)) {
      this.toastr.error('ðŸ‘‹ Jelly-o marshmallow marshmallow cotton candy dessert candy.', 'Negative Stock!', {
        toastClass: 'toast ngx-toastr',
        closeButton: true
      });
      return;
    }
    this._wholeSaleService.create(formData).subscribe(
      (res) => {
        this._sw.fire(
          "success",
          "Whole Sale Invoice",
          `Record Successfully Add..`
        );
        this.submitted = false;
        this.wholeSaleForm.reset();
      },
      (err) => {
        this._sw.fire("error", "Item Master", err.error.message);
      }
    );
  }

  updateFormDetail(formData) {
    this._wholeSaleService.update({ id: this.id, ...formData }).subscribe(
      (res) => {
        this._sw.fire(
          "success",
          "Whole Sale Invoice",
          `Record Successfully Update..`
        );
        this.editable = false;
        this.submitted = false;
        this._router.navigate(["/whole-sale"]);
      },
      (err) => {
        this._sw.fire("error", "Item Master", err.error.message);
      }
    );
  }

  navigateToBack() {
    this._router.navigate(["/whole-sale"]);
  }

  AddCustomer = () => {
    const modelOpen = this.modalService.open(ModalAddCustomerComponent, {
      centered: true,
      windowClass: "modal modal-primary",
      size: "xl",
    });
    modelOpen.closed.subscribe((close) => this.getUserDetails());
  };

  getUserDetails() {
    this._customerService.getAll().subscribe((res) => {
      if (res.data) {
        this.userDetail = res.data.filter(
          (type) => type.customerType == "Wholesale Fabric Supplier"
        );
      }
    });
  }

  getItemMaster() {
    this._itemMaster.getAll().subscribe((res) => {
      if (res.data) {
        this.itemMaster = res.data;
      }
    });
  }

  getColorType() {
    this._colorType.getAll().subscribe((res) => {
      if (res.data) {
        this.colorType = res.data;
      }
    });
  }

  getTotalQuantity() {
    let count = 0;
    this.items.map((getQty) => (count += getQty.itemQuantity));
    this.totalQuantity = count;
    this.shipmentCalculator();
  }

  getTotalNetAmount() {
    let amountTotal = 0;
    this.items.map((getQty) => (amountTotal += getQty.itemNetAmount));
    this.totalAmount.totalNetAmount = amountTotal;
  }

  selectCustomer(value) {
    this.customerData.customerName = value.full_name;
    this.customerData.customerNumber = value.phone;
  }

  detailCalculator() {
    this.items.forEach((x, i) => {
      this.items[i].itemAmount =
        this.items[i].itemPrice * this.items[i].itemQuantity;
      this.items[i].itemNetSalePrice =
        (this.items[i].itemAmount + +this.items[i].itemDelivery) /
        this.items[i].itemQuantity;
      this.items[i].itemNetAmount =
        this.items[i].itemAmount + +this.items[i].itemDelivery;
    });
    this.getTotalNetAmount();
    this.totalAmountCalculator();
  }

  shipmentCalculator() {
    this.items.forEach((x, i) => {
      this.items[i].itemDelivery = Math.round(
        (this.totalAmount.totalDeliveryCost / this.totalQuantity) *
          this.items[i].itemQuantity
      );
    });
  }

  totalAmountCalculator() {
    this.totalAmount.netBalanceAmount =
      this.totalAmount.totalNetAmount - this.totalAmount.amountReceived;
  }

  openImagePopup(i) {
    const openModel = this.modalService.open(ImageModalComponent, {
      centered: true,
      windowClass: "modal modal-primary",
      size: "lg",
    });
    openModel.componentInstance.modalHeader = "WholeSale Image";
    openModel.componentInstance.imgSrc =
      this.editable == true ? this.editOrderImage[i] : this.orderImages[i];
  }

  deleteImageOrder(i) {
    this.wholeSaleForm.controls.details.value[i].itemImage = null;
    this.orderImages[i] = null;
    this.items[i].itemImage = null;
    this.editOrderImage[i] = null;
  }
}
